import baseUrl from '../../utils/utils.js'
export default {
	data() {
		return {
			userInfo:[],
			show:false,
			curIndex:'',
			text:"已设为默认",
			text1:"设为默认",
			top:"取消置顶",
			top1:"置顶",
			curIndex1:'',//记录点击使用的下标
			username:"",
			phone:"",
			address:"",
			detail:"",
			method:"",
			editIndex:''
		}
	},
	methods: {
		// 默认
		ctrl(e){
			var that=this
			uni.request({
				url:baseUrl+'Address/setDefAdd',
				method:'POST',
				data:{
					address_id:this.userInfo[e.currentTarget.dataset.index].address_id
				},
				success(ev){
					console.log(ev)
					for(var i=0;i<that.userInfo.length;i++){
						that.userInfo[i].is_default=0
					}
					that.userInfo[e.currentTarget.dataset.index].is_default=1
				}
			})
			
		},
		// 使用
		use(e){
			this.curIndex1=e.currentTarget.dataset.index
			uni.setStorage({
				key:"userInfo",
				data:JSON.stringify(this.userInfo[this.curIndex1])
			})
			uni.setStorage({
				key:"useIndex",
				data:this.curIndex1
			})
			wx.navigateBack()
		},
		// 置顶
		toTop(e){
			var arr=this.userInfo
			for(var i=0;i<this.userInfo.length;i++){
				if(i==e.currentTarget.dataset.index){
					this.userInfo[i].top=!this.userInfo[i].top
					var obj=arr[i]
					if(this.userInfo[i].top){
						arr.splice(e.currentTarget.dataset.index,1)
						arr.unshift(obj)
					}else{
						arr.splice(e.currentTarget.dataset.index,1)
						arr.push(obj)
					}
				}
			}
			
		},
		// 删除收货款地址
		del(e){
			// console.log(this.userInfo[e.currentTarget.dataset.index])
			wx.showModal({
				title: '提示',
				content: "确定删除吗？",
				success: res => {
					if (res.confirm) {
						
						// 如果删除的是当前使用的地址
						if(this.userInfo[e.currentTarget.dataset.index].use==true){
							// 调用删除接口
							uni.setStorage({
								key:"useIndex",
								data:-1
							})
						}
						uni.request({
							url:baseUrl+'Address/delAddress',
							method:"POST",
							data:{
								address_id:this.userInfo[e.currentTarget.dataset.index].address_id
							}
						})
						this.userInfo.splice(e.currentTarget.dataset.index,1)
						
						
					}else if (res.cancel) {
						console.log("取消")
					}
				},
			})
			
		},
		// 添加收货地址
		add(e){
			this.username=""
			this.phone=""
			this.address=""
			this.detail=""
			this.method=e.currentTarget.dataset.method
			this.show=true
		},
		onClose(){
			this.show=false
		},
		// 用户名
		onChangeU(e){
			this.username=e.detail
		},
		// 电话
		onChangeP(e){
			this.phone=e.detail
		},
		// 选择地址
		changeAdd(e){
			this.address=e.detail.value[0]+" "+e.detail.value[1]+" "+e.detail.value[2]
		},
		// 详细地址
		onChangeA(e){
			this.detail=e.detail
		},
		// 编辑地址
		edit(e){
			this.method=e.currentTarget.dataset.method
			this.show=true
			this.editIndex=e.currentTarget.dataset.index
			this.username=this.userInfo[e.currentTarget.dataset.index].consignee
			this.phone=this.userInfo[e.currentTarget.dataset.index].mobile
			this.address=this.userInfo[e.currentTarget.dataset.index].p_c_d
			this.detail=this.userInfo[e.currentTarget.dataset.index].detail
		},
		// 保存
		save(e){
			console.log(this.method)
			if(this.method=='add'){
				var that=this
				// 调用添加地址接口
				uni.request({
					url:baseUrl+'Address/createAddress',
					method:"POST",
					data:{
						consignee:this.username,
						mobile:this.phone,
						p_c_d:this.address,
						detail:this.detail,
						uorc:0
					},
					
					// 重新调用地址列表接口
					success(e){
						uni.request({
							url:baseUrl+'Address/getUserAddress',
							method:'POST',
							success(e){
								console.log(e.data.data)
								that.userInfo=e.data.data
							}
						})
					}
				})
				this.show=false
				
				
			}else if(this.method=="edit"){
				// 编辑地址
				var arr = this.userInfo
				var that=this
				uni.request({
					url:baseUrl+'Address/createAddress',
					method:'POST',
					data:{
						consignee:this.username,
						mobile:this.phone,
						p_c_d:this.address,
						detail:this.detail,
						address_id:arr[this.editIndex].address_id,
						uorc:1
					},
					// 重新调用地址列表接口
					success(e){
						uni.request({
							url:baseUrl+'Address/getUserAddress',
							method:'POST',
							success(e){
								console.log(e.data.data)
								that.userInfo=e.data.data
							}
						})
					}
				})
				this.show=false
			}
		}
	},
	onLoad(){
		var that=this
		var arr=[]
		uni.request({
			url:baseUrl+'Address/getUserAddress',
			method:'POST',
			success(ev){
				arr=ev.data.data
				uni.getStorage({
					key:"useIndex",
					success(e){
						if(arr[e.data]){
							arr[e.data].use=true
						}
						that.userInfo=arr
						console.log(that.userInfo)
					},
					fail(){
						that.userInfo=arr
					}
				})
				
			}
		})	
	}
}
